<template>
  <img alt="Vue logo" src="./assets/name.png">
  <br>
  <button v-on:click="showdata">ดู ชื่อ-นามสกุล</button>
  <button @click="showdataa">ดู ชื่อ</button>
  <button @click="showdataaa">ดู นามสกุล</button>
  <h1>{{firstname}}  {{lastname}}</h1>
  <h1>{{namee}}</h1>

<br>
  <h1>{{age}}</h1>
<br>
  <button @click="pass">UP</button>
  <button v-on:click="down">DOWN</button>
  <button v-on:click.ctrl="UPX2(6)">UPP</button>
  <button v-on:click.right="DONX2(10)">DOWNN</button>
<br>
  <form >
    <label for=""></label>แสดงชื่อจริง : <input type="text" @input="setnickname"><br>
    <label for=""></label>แสดงชื่อนามสกุล : <input type="text" @input="setnickname2"><br>
    <label for=""></label>แสดงชื่อเล่น : <input type="text" @input="setnickname3"><br>
    <label for=""></label>แสดงวัน/เดือน/ปีเกิด : <input type="text" @input="setnickname4"><br>
    <label for=""></label>แสดงสาขา : <input type="text" @input="setnickname5"><br>
    <label for=""></label>แสดงห้อง  : <input type="text" @input="setnickname6"><br>
    <label for=""></label>แสดงคติประจำใจ : <input type="text" @input="setnickname7"><br>
    <label for=""></label>แสดงวิชาที่ไม่ชอบ :  : <input type="text" @input="setnickname8"><br>
    <button type="submit">ตกลง</button>
  </form>
  <h1>แสดงชื่อจริง :{{nickmane}}</h1>
  <h1>แสดงชื่อนาสกุล :{{nickmane2}}</h1>
  <h1>แสดงชื่อเล่น :{{nickmane3}}</h1>
  <h1>แสดงวัน/เดือน/ปีเกิด :{{nickmane4}}</h1>
  <h1>แสดงสาขา :{{nickmane5}}</h1>
  <h1>แสดงห้อง :{{nickmane6}}</h1>
  <h1>แสดงคติประจำใจ :{{nickmane7}}</h1>
  <h1>แสดงวิชาที่ไม่ชอบ :{{nickmane8}}</h1>
</template>

<script>

export default {
  namee:"123",
  data(){
    return{
      firstname : "Napat",
      lastname : "Surin",
      age : 20,
      nickmane:"",
      nickmane2:"",
      nickmane3:"",
      nickmane4:"",
      nickmane5:"",
      nickmane6:"",
      nickmane7:"",
      nickmane8:""


    }
  },
  methods:{
      showdata(){
        alert(this.firstname+this.lastname)
      },
      showdataa(){
        alert(this.firstname)
      },
      showdataaa(){
        alert(this.lastname)
      },
      pass(){
        this.age++
      },
      down(){
        this.age--
      },
      UPX2(val){
        this.age+=val
      },
      DONX2(val){
        this.age-=val
      },
      setnickname(event){
        // console.log(event.target.value) เอาไว้เช็คดูการตรวจสอบป้อนข้อมูล
        this.nickmane = event.target.value
      },
      setnickname2(event){
        this.nickmane2 = event.target.value
      },
      setnickname3(event){
        this.nickmane2 = event.target.value
      },
      setnickname4(event){
        this.nickmane4 = event.target.value
      },
      setnickname5(event){
        this.nickmane5 = event.target.value
      },
      setnickname6(event){
        this.nickmane6 = event.target.value
      },
      setnickname7(event){
        this.nickmane7 = event.target.value
      },
      setnickname8(event){
        this.nickmane8 = event.target.value
      }
  }
}
</script>

